	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Who Do I Sue?","date":"28 Feb 2007","slug":"/2007-02-28-who-do-i-sue/","tags":"Y2007"},"html":"<p>I've just been informed that the German technology magazine <a href=\"http://www.heise.de/ct/\">c/t</a> has without our permission published my and Christopher's picture in their latest issue (05/2007, p. 70), in the screen shot of a feed reader. So who do I sue? The <a href=\"http://www.schockwellenreiter.de/2007/02/09.html#harzigenGlhstrumpf\">original post</a> comes from Jörg Kantel's <a href=\"http://www.schockwellenreiter.de/\">Der Schockwellenreiter</a>, but he works for a public institution, so there are no deep pockets there. But <a href=\"http://vowe.net/\">Volker Weber</a> writes for c/t, maybe I can score one of his Blackberries....</p>"},"next":{"fields":{"title":"Organic Foods","date":"28 Feb 2007","slug":"/2007-02-28-organic-foods/"}},"previous":{"fields":{"title":"Mickey Mouse Outfit","date":"04 Mar 2007","slug":"/2007-03-04-mickey-mouse-outfit/"}}},{"node":{"fields":{"title":"Organic Foods","date":"28 Feb 2007","slug":"/2007-02-28-organic-foods/","tags":"Y2007"},"html":"<p><a href=\"http://www.flickr.com/photos/papascott/405607265/\" title=\"Photo Sharing\"><img src=\"http://farm1.static.flickr.com/178/405607265_ca466deb83.jpg\" width=\"500\" height=\"375\" alt=\"28022007073\" /></a></p>\n<p>These days *Bio* or organic food is very much in trend in Germany, but this is one item I was happy to see not to be \"all natural\". Chocolate lamb pellets from the island of Sylt.</p>"},"next":{"fields":{"title":"","date":"26 Feb 2007","slug":"/2007-02-26-lothar-gunther-buchheim/"}},"previous":{"fields":{"title":"Who Do I Sue?","date":"28 Feb 2007","slug":"/2007-02-28-who-do-i-sue/"}}},{"node":{"fields":{"title":"","date":"26 Feb 2007","slug":"/2007-02-26-lothar-gunther-buchheim/","tags":"Y2007"},"html":"<p>Lothar-Günther Buchheim, the author of \"Das Boot\", <a href=\"http://www.nytimes.com/2007/02/24/obituaries/24buchheim.html\">died last week aged 89</a>. The film, but especially <a href=\"http://www.amazon.co.uk/Das-Boot-Lothar-Gunther-Buchheim/dp/0304352314/\">the book (in English translation)</a>, were very important to me  when it became clear that I would be spending a permanent relationship with a Ger(wo)man, and with a nation full of Nazis (so I thought). The novel shows the pointlessness of war, the humanity of the sailors, and the extreme claustrophia aboard the U-Boat. Thanks to <a href=\"http://www.dangerousmeta.com/?p=12347\">Garret for posting the obituary</a>.</p>"},"next":{"fields":{"title":"Attempted Denial-of-Service Attack","date":"24 Feb 2007","slug":"/2007-02-24-attempted-denial-of-service-attack/"}},"previous":{"fields":{"title":"Organic Foods","date":"28 Feb 2007","slug":"/2007-02-28-organic-foods/"}}}],"pathPrefix":"","first":false,"last":false,"index":151,"pageCount":1101}}

